.single-product {
  display: flex;
  justify-content: space-around;
  text-align: center;
  white-space: wrap;
  padding: 40px;
}
@media only screen and (max-width: 600px) {
  .single-product {
    flex-direction: column;
    align-items: center;
  }
  .paypal-wrapper {
    width: 100%;
  }
}
.paypal-wrapper {
  width: 30%;
  margin: 20px 0;
}
