.card {
  width: 250px;
  background: #fff;
  box-shadow: 6px 7px 25px -6px rgba(0, 0, 0, 0.58);
}
.img-box {
  width: 100%;
  height: 310px;
  position: relative;
  overflow: hidden;
}
.img-box img {
  padding: 10px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s ease-in-out;
  transform-origin: right;
}
.card:hover img {
  transform: scale(1.5);
}
.action {
  position: absolute;
  top: 10px;
  right: 10px;
}
.action li .cart-icon {
  color: #333;
}
.action li {
  position: relative;
  list-style-type: none;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  margin: 4px;
  cursor: pointer;
  transform: translateX(50px) translateY(50px);
  transition: 0.5s;
}
.card:hover .action li {
  transform: translateX(0) translateY(0);
}
.action li:hover {
  background: #ff2020;
}
.action li:hover .cart-icon {
  color: #fff;
}
.action li span {
  position: absolute;
  right: 50px;
  top: 50%;
  background: #fff;
  border-radius: 4px;
  transform: translateY(-50%) translateX(-20px);
  white-space: nowrap;
  color: #333;
  padding: 4px 10px;
  font-weight: 500;
  font-size: 12px;
  pointer-events: none;
  opacity: 0;
  transition: 0.5s;
}
.action li:hover span {
  opacity: 1;
  transform: translateX(0) translateY(-50%);
}
.content {
  padding: 10px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.product-description {
  padding: 20px;
  font-size: 24px;
  font-weight: 400;
}
.product-reviews {
  color: #6e6e6e;
  padding: 20px;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.4em;
}
.content .product-name h3 {
  font-size: 18px;
  font-weight: 500;
  color: #333;
}
.content .price-rating {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.price-rating h2 {
  font-size: 20px;
  font-weight: 500;
  color: #ff2020;
}
