@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
body {
  display: flex;
  align-content: center;
  justify-content: center;
  min-height: 100vh;
  background: #ebebeb;
  overflow-x: hidden;
}
